          height="100%"
          border
          style="width: 100%" class="table">

          <el-table-column
            prop="task_id"
            label="id">
          </el-table-column>
          <el-table-column
            prop="city_name"
            label="城市名称">
          </el-table-column>
          <el-table-column
            prop="city_code"
            label="城市2字码">
          </el-table-column>
          <el-table-column
            class-name="markColor"
            prop="exchange_rate"

            label="汇率">
          </el-table-column>
          <el-table-column
            class-name="markColor"
            prop="is_del"
            label="是否启用">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>
              <el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalSize">
        </el-pagination>
      </div>
    </div>
</template>

