        }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      addTask() {
        this.$router.push('/config/addTask')
      },
      handleEdit(row) {
        this.$router.push({ path: '/config/updateTask',query: row})
      },
      handleDelete(row,index) {
        let url = '/v1/translations/'+row.task_id;
        let that = this;
        this.$confirm('是否确认删除该条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$axios.delete(url).then((res) => {
            if(res.data.code == 200){
              that.tableData.splice(index,1);
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          }).catch(() => {
            this.$message({
              type: 'error',
              message: '删除失败!'
            });
          })
        }).catch(() => {});
      },
      initTable() {
        let initList = [];
        let url = "/v1/salescitys?page="+this.currentPage+"&pagesize="+this.pageSize;
        let that = this;
        this.$axios.get(url).then((res) => {
          if(res.data.code == 200){
            let tasksList = res.data.data.list;
            that.totalSize = parseInt(res.data.data.page_total);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.task_id = task.id;
              tempTask.city_code = task.city_code;
              tempTask.city_name = task.city_name;
              tempTask.is_del =del[task.is_del];
              tempTask.exchange_rate = task.exchange_rate;

              initList.push(tempTask);
            })
          }
          that.tableData = initList;
          console.log(res)
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
      this.initTable();
    }
  }
